html {
  width: 100vw;
  height: 100vh;
  background-image: url("images/phuchifa.jpg");
  background-size: 110%;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.App {
  text-align: center;
  height: 100vh;
  width: 100vw;
}

h2, h3, h4, h5, p {
  font-family: 'Nixie One';
}

p {
  font-size: calc(5px + 2vmin);
}

#navbar {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  font-size: calc(7px + 2vmin);
}

.App-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-left: 50px;
  padding-right: 50px;
  margin: 100px;
  font-size: calc(7px + 2vmin);
  color: black;
}

.Section-header {
  display: flex;
  flex-direction: column;
  margin-left: 50px;
  font-size: calc(7px + 2vmin);
  color: black;
}

#aboutme {
  display: flex;
  flex-direction: row;
  margin: 50px;
}

.section_container {
  flex-direction: column;
  max-width: 60%;
  background-color: rgb(208, 233, 265, .4);
}

#section {
  display: flex;
  flex-direction: row;
  margin-left: 20px;
  margin-right: 20px;
  justify-content: center;
  font-size: calc(5px + 2vmin);
}

#section_title {
  margin-left: 30px;
  margin-right: 30px;
  font-size: calc(7px + 2vmin);
  /* flex: 3; */
}

#projects_title {
  margin-left: 50px;
  margin-top: 50px;
}


#projects {
  display: flex;
  flex-direction: column;
}

.single_project {
  flex-direction: column;
  margin-left: 300px;
  margin-right: 300px;
}

.project_item {
  margin-top: 30px;
  margin-bottom: 30px;
}

Embed {
  align-self: center;
  justify-content: center;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .App-header, .section {flex-direction: column;}
}

.profile_photo {
  border-radius: 175px;
  margin-left: 50px;
  margin-top: 50px;
}

.photos {
  margin-right: 50px;
  flex-grow: 2;
}

.about_me_photo {
  margin-right: 15px;
  margin-left: 15px;
  margin-top: 40px;
  justify-content: center;
  width: 100%;
  height: auto;
  align-self: flex-end;
}

.text {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  padding-left: 30px;
  padding-right: 30px;
}

#logos {
  flex-direction: row;
}

.logo {
  width: 50px;
  margin: 15px;
}

#linkedin.logo:hover
{
  webkit-transform: rotateZ(-30deg);
  ms-transform: rotateZ(-30deg);
  transform: rotateZ(-30deg);
}

#github.logo:hover
{
  webkit-transform: rotateZ(30deg);
  ms-transform: rotateZ(30deg);
  transform: rotateZ(30deg);
}

.App-link {
  color: #09d3ac;
}

.all_pages {
  display: flex;
  flex-direction: row;
}

.pages {
  margin-left: 20px;
  margin-right: 20px;
}

@-webkit-keyframes swing
{
    15%
    {
        webkit-transform: translateX(5px);
        transform: translateX(5px);
    }
    30%
    {
        webkit-transform: translateX(-5px);
       transform: translateX(-5px);
    }
    50%
    {
        webkit-transform: translateX(3px);
        transform: translateX(3px);
    }
    65%
    {
        webkit-transform: translateX(-3px);
        transform: translateX(-3px);
    }
    80%
    {
        webkit-transform: translateX(2px);
        transform: translateX(2px);
    }
    100%
    {
        webkit-transform: translateX(0);
        transform: translateX(0);
    }
}
@keyframes swing
{
    15%
    {
        webkit-transform: translateX(5px);
        transform: translateX(5px);
    }
    30%
    {
        webkit-transform: translateX(-5px);
        transform: translateX(-5px);
    }
    50%
    {
        webkit-transform: translateX(3px);
        transform: translateX(3px);
    }
    65%
    {
        webkit-transform: translateX(-3px);
        transform: translateX(-3px);
    }
    80%
    {
        webkit-transform: translateX(2px);
        transform: translateX(2px);
    }
    100%
    {
        webkit-transform: translateX(0);
        transform: translateX(0);
    }
}

.pages:hover
{
        webkit-animation: swing 1s ease;
        animation: swing 1s ease;
        webkit-animation-iteration-count: 1;
        animation-iteration-count: 1;
}
